@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #e4e4e4;
  padding: 20px;
}

.note {
  width: 300px;
  height: 200px;
  position: relative;
  background-color: #a4ecce;
  box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.note-header {
  position: absolute;
  top: 5px;
  right: 10px;
}

.btn {
  color: #898989;
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
}

.btn:first-child {
  margin-right: 5px;
}

.btn-add {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  bottom: 20px;
  right: 20px;
  width: 180px;
  height: 50px;
  border: none;
  font-family: inherit;
  font-weight: 300;
  border-radius: 40px;
  background-color: rgba(164, 236, 206, 0.8);
  color: white;
  font-size: 18px;
  cursor: pointer;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.btn-add:hover {
  background-color: #a4ecce;
}

.fa-plus-circle {
  font-size: 26px;
}

.note-main {
  width: 100%;
  height: 100%;
  padding: 15px;
}

textarea {
  width: 100%;
  height: 100%;
  background-color: #8ad6b7;
  border: none;
  font-family: inherit;
  padding: 15px;
  outline: none;
}

.is-hidden {
  display: none;
}
